:root {
    --background: #141218;
    --offset-base-size: 2px;

    --common-border-radius: 8px;

    --text-color-primary: #19192D;
    --text-color-secondary: #FFF7E9;

    --text-color-h1: #FFF7E9;;
    --text-color-black: #141218;;
    --text-color-input: #FFF7E9;
    --text-input-placeholder: #7a7a7a;

    --bg-input-hover: #fafafa;
    --bg-input-disabled: #ebebeb;

    --red-bg-color: #F46C5C;

    --bg-button-default: #FFF7E9;
    --bg-button-hover: #9D755B;
    --bg-button-active: #319b88;
    --bg-button-disabled: #3A394F;

    --default-color: #e0b351;
    --changing-color: rgb(75, 25, 25);
    --modified-color: #e0b351;

    --common-transition: all 0.4s ease-in;
}


body {
    font-family: -apple-system, Roboto,
    Helvetica Neue, Arial, Noto Sans, Liberation Sans, sans-serif,
    Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 0;
    margin: 0;
    color: var(--text-color-primary);
    background: var(--background);
}

/* Typography classes */

.text {
    margin: 0;
    padding: 0;
}

.text_color_primary {
    color: var(--text-color-primary);
}

