.content {
    background-size: cover;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: flex-start;
}

.titleBox {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 100px auto 0;
    align-items: center;
}

.contentCard {
    display: flex;
    flex-direction: column;
    align-content: baseline;
    justify-items: flex-start;
    background: #141218;
    margin: 0 20px 0px;
    padding: 30px;
    box-sizing: border-box;
}

.returnButton {
    z-index: 2;
}

.cardTitle {
    display: flex;
    justify-content: center;
    margin-bottom: 0;
    flex-direction: row;
}

.subtext {
    margin: 30px auto 0;
    text-align: center;
    padding-bottom: 60px;
    max-width: 950px;
    min-width: 800px;
    z-index: 2;
}

.link {
    display: flex;
    text-decoration: none;
    height: 100px;
    width: 950px;
    background: linear-gradient(-45deg, rgba(242, 223, 186, 0.17), rgba(43, 116, 108, 0.25), rgba(208, 77, 61, 0.27), rgba(106, 104, 128, 0.55));
    animation: gradient 200ms ease-in-out infinite;
    background-size: 200% 200%;
    align-content: center;
    justify-content: center;
    margin: 20px 30px 50px;
    align-self: center;
    z-index: 2;
}

.titledContainer {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    justify-content: space-between;
    justify-self: center;
    width: 900px;
    z-index: 2;
}

.decoraionLine {
    transition: var(--common-transition);
    background-image: url(../../../images/DecorLine.svg);
    height: 282px;
    width: 1440px;
    position: relative;
    z-index: 1;
    opacity: 0.3;
}

.decoraionLine:hover {
    opacity: 0.6;
}

.decoraionLine_top {
    margin-bottom: -280px;
    top: -100px;
}
