.button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    outline: none;
    border-radius: var(--common-border-radius);
    background: linear-gradient(-45deg, rgba(242, 223, 186, 0.17), rgba(43, 116, 108, 0.25), rgba(208, 77, 61, 0.27), rgba(106, 104, 128, 0.55));
    animation: gradient 8s ease infinite;
    background-size: 400% 400%;
    padding: 18px 24px 19px 24px;
    transition: var(--common-transition);
    height: 80px;
    min-width: 190px;
}



@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


.button:hover:enabled {
    cursor: pointer;
    animation: none;
}

.button:active:enabled {
    background: var(--bg-button-active);
    color: var(--text-color-secondary);
    cursor: pointer;
    fill: var(--text-color-secondary);

}

.button:disabled {
    background: var(--bg-button-disabled);
}

.big {
    width: 362px;
}

.loader:disabled {
    background-color: var(--bg-button-default);
}

.loader_icon {
    animation: rotation 1s linear infinite normal forwards running;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
