.mainContainer {
    display: flex;
    flex-direction: column;
    gap: 78px;
    align-items: center;
}

.inputContainer {
    max-width: 829px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 20px;
}

.input {
    width: 100%;
}

.button {
    display: flex;
}

.inputSection {
    display: flex;
    width: 100%;
    justify-content: space-between;
    column-gap: 12px;
}

.addButton {
    display: flex;

}

.deleteButton {
    display: flex;
    background: linear-gradient(45deg, #d04d3d, #2b746c, #6a6880, #f2dfba);
    animation: gradient 8s ease infinite;
    height: 80px;
    clip: auto;
}

.circlesBox {
    display: flex;
    column-gap: 16px;
    padding: 0;
    margin-bottom: 40px;
    max-width: 944px;
    width: 100%;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: center;
    row-gap: 68px;
}
