@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600;900&display=swap");


@font-face {
    font-family: 'JOURNALISM';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url("Journalism/Journalism.ttf") format("ttf"),
    url("Journalism/Journalism.woff") format("woff"),
    url("Journalism/Journalism.woff2") format("woff2"),
    url("Journalism/Journalism.otf") format("otf");
}

@font-face {
    font-family: 'Amatic SC';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url("Amatic/AmaticSC-Regular.ttf") format("ttf"),
    url("Amatic/AmaticSC-Bold.woff") format("woff"),
    url("Amatic/AmaticSC-Bold.woff2") format("woff2"),
    url("Amatic/AmaticSC-Bold.otf") format("otf");
}

@font-face {
    font-family: 'Amatic SC';
    font-style: normal;
    font-weight: 900;
    font-display: swap;
    src: url("Amatic/AmaticSC-Bold.ttf") format("ttf"),
    url("Amatic/AmaticSC-Bold.woff") format("woff"),
    url("Amatic/AmaticSC-Bold.woff2") format("woff2"),
    url("Amatic/AmaticSC-Bold.otf") format("otf");
}
