.content {
    margin-top: 100px;
    background-color: #141218;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.title_box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 1280px;
    margin: 0 auto;
}

.title {
    display: inline-block;
    line-height: 100px;
    z-index: 2;
}

.cards_box {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    max-width: 1240px;
    margin: 32px 0 80px;
    align-self: center;
    z-index: 2;
}

.card {
    width: 1000px;
    height: 400px;
    cursor: pointer;
    transition: var(--common-transition);
    border-radius: 20px;
    z-index: 2;
}

.arr {
    background-image: url("../../images/cover2.svg");
    background-repeat: no-repeat;
}

.arr:hover {
    background-image: url("../../images/cover7.svg");
    background-repeat: no-repeat;
}

.stack {
    background-image: url("../../images/cover4.svg");
    background-repeat: no-repeat;
}

.stack:hover {
    background-image: url("../../images/cover8.svg");
    background-repeat: no-repeat;
}

.queue {
    background-image: url("../../images/cover5.svg");
    background-repeat: no-repeat;
}

.queue:hover {
    background-image: url("../../images/cover6.svg");
    background-repeat: no-repeat;
}

.list {
    background-image: url("../../images/cover1.svg");
    background-repeat: no-repeat;

}

.list:hover {
    background-image: url("../../images/cover3.svg");
    background-repeat: no-repeat;

}


.subtext {
    margin: 28px auto 0;
    text-align: center;
    padding-bottom: 18px;
    max-width: 1240px;
    min-width: 1240px;
}

.decoraionLine {
    transition: var(--common-transition);
    background-image: url(../../images/DecorLine.svg);
    height: 282px;
    position: relative;
    z-index: 1;
    opacity: 0.3;
}

.decoraionLine:hover {
    opacity: 0.8;
}

.decoraionLine_top {
    margin-bottom: -200px;
    top: -600px;
}

.decoraionLine_middle {
    margin-top: -200px;
    top: 600px;
}

.decoraionLine_bottom {
    bottom: 280px;
    margin-bottom: -280px;
}